/* # Global styles

All the global styles must be kept together here, and all the scoped styles in
their own files.


## Global vs. scoped styles

The selector `ul` is global because it will find all unordered lists no
matter where they are.

To scope this selector you would prefix it with a class, and that class would
become its scope. Like this: `.article ul`. Now only the lists within an
`.article` will be found.

Only classes creates scope, all other selectors are considered global selectors.

These selectors are global:

* `button`
* `form button`
* `input[type=button]`
* `*:last-child`

These selectors are scoped:

* `.article`
* `.js button`
* `.no-js .article code`
* `[class^="icon-"], [class*=" icon-"]`
